<template>
    <div class="withdraw">
        <nav-bar
            title="提现兑换"
            :hairline="false">
            <div
                v-if="pageType!='CHANNEL_NEW_USER'"
                @click="goManualHandler">兑换说明</div>
        </nav-bar>
        <withdraw-wechat
            v-bind="{
                isVisitor: isVisitor(),
                isAuth,
                isWechatAuth,
                withdrawInfo,
                authPhonenumber,
                newImei,
            }"
            @clickGuideHand='clickGuideHand'
            @login="init"
        />
    </div>
</template>
<script>
import { getWithdrawPageInfo, getBubble } from '@/api/withdraw';
import * as withdrawAuth from '@/api/withdrawAuth';
import NavBar from '@/components/NavBar';
import * as event from '@/utils/event';
import miVerify from '@/utils/miVerify';
import {
    isTakeOverBackByWeb,
    openCommonWebViewByPath,
    getUserAccountInfo,
    quitCurrentWebview,
} from '@/utils/jsApiV2/client';
import config from '@/config';
import { get, set, remove } from '@/utils/cache';
import { getServerConfigByKey } from '@/api/common';
import WithdrawWechat from './WithdrawWechat';
import { cacheUserLevelKey, pageType } from './config';
import '../../../static/v';

const bubbleNewImei = 'bubbleNewImei';

export default {
    name: 'Withdraw',
    components: {
        NavBar,
        WithdrawWechat,
    },
    data() {
        return {
            defaultUserIcon: config.default.userIcon,
            identityInfo: {},
            withdrawInfo: {},
            newImei: false, // 新IMEI首次进入h5页面
            isUpdate: false,
            pageType: this.$route.query.channel || 'NORMAL',
        };
    },
    computed: {
        isAuth() {
            return this.identityInfo.auth || false;
        },
        isWechatAuth() {
            return this.identityInfo.isWechatAuth;
        },
        authPhonenumber() {
            return this.identityInfo.phoneNumber;
        },
        canCloseWebview() {
            return !this.newImei;
        },
    },
    created() {
        Promise.all([
            this.isShowUpdate(),
            this.getWithdrawInfo(),
        ])
            .then(() => {
                if (!this.isUpdate) {
                    this.$root.hidePlaceholder();
                }
            })
            .catch(this.$error);
        this.getIdentityInfo();
        this.getBubble();
        // 人机验证初始化
        miVerify(
            { type: '时长任务' },
            (verify) => {
                window.miVerify.withdraw.verify = verify;
            },
            'withdraw',
        );
        // 设置支付渠道，这里主要是为了在实名认证结果页调用
        if (this.pageType) {
            set(pageType, this.pageType);
            set('taskday', this.$route.query.taskday);
        } else {
            remove(pageType);
            remove('taskday');
        }
        event.addCustomEventListener('focus', this.init);
        isTakeOverBackByWeb(true);
        event.addCustomEventListener('backKey', this.backKeyHandler);
        const o2oObj = { e_c: '提现', e_a: '曝光', e_n: '提现页面', e_x: { source: this.$route.query.source || '' } };
        if (this.pageType === 'CHANNEL_NEW_USER') {
            o2oObj.e_c = '裂变新手链路提现';
            o2oObj.e_x = {
                task_day: this.$route.query.taskday,
            };
        }
        this.$o2o(o2oObj);
    },
    destroyed() {
        isTakeOverBackByWeb(false);
        event.removeCustomEventListener('backKey', this.backKeyHandler);
    },
    methods: {
        init() {
            this.getIdentityInfo();
            this.getWithdrawInfo();
            this.getBubble();
        },
        getIdentityInfo() {
            return withdrawAuth.get({ forceAuth: true })
                .then((data) => {
                    this.identityInfo = { ...data };
                });
        },
        getWithdrawInfo() {
            return getWithdrawPageInfo({ pageType: this.pageType })
                .then((data) => {
                    this.withdrawInfo = { ...data };
                    set(cacheUserLevelKey, this.withdrawInfo.userLevel);
                });
        },
        getBubble() {
            return getBubble({
                type: 'withdrawGuide',
            }).then((res) => {
                this.newImei = res.showBubble && !get(bubbleNewImei);
                if (this.newImei) {
                    set(bubbleNewImei, true);
                }
            });
        },
        isShowUpdate() {
            if (config.versionCode && Number(config.versionCode) < 40301) {
                this.goUpdate();
            } else {
                // TODO: 4.7.90 汇率调整提现功能暂时下线
                return getServerConfigByKey('withdrawwhitelist')
                    .then((data) => {
                        if (data !== true) {
                            this.goUpdate();
                        }
                    })
                    .catch(() => {
                        this.goUpdate();
                    });
            }
            return Promise.resolve({ msg: 'config.versionCode < 40301' });
        },
        goUpdate() {
            this.isUpdate = true;
            this.$router.push({ name: 'updateTip' });
        },
        clickGuideHand() {
            this.newImei = false;
        },
        backKeyHandler() {
            // 出现蒙层 回退无效  否则关闭当前页面
            if (this.canCloseWebview) {
                quitCurrentWebview();
            }
        },
        goManualHandler() {
            openCommonWebViewByPath('/mobile-v2/withdraw/withdraw-manual');

            this.$o2o({ e_c: '提现', e_a: '点击', e_n: '兑换说明' });
        },
        isVisitor() {
            return !!getUserAccountInfo().isTourist;
        },
    },
};
</script>
<style scoped>
.withdraw {
    display: flex;
    flex-direction: column;
    height: 100%;
}
</style>
<style>
body .miverify_wind {
    opacity: 0 !important;
}
</style>
