<template>
    <div class="withdraw__input-container">
        <input
            type="tel"
            class="withdraw__input"
            placeholder="请输入要充值的手机号码"
            maxlength="13"
            ref="input"
            :value="formattedPhoneNumber"
            @input="phoneNumberInputHandler">
        <a
            v-if="phoneNumber"
            href="javascript:"
            class="withdraw__clear-btn"
            @click="clearHandler" />
    </div>
</template>

<script>
export default {
    name: 'WithdrawPhoneInput',
    data() {
        return {
            phoneNumber: '',
            formattedPhoneNumber: '',
        };
    },
    props: {
        value: {
            type: String,
            default: '',
        },
    },
    mounted() {
        this.phoneNumber = this.value;
        this.focusInput();
    },
    watch: {
        phoneNumber(newValue) {
            this.formattedPhoneNumber = newValue.replace(/[^\d ]/g, '');
            let pos = this.$refs.input.selectionStart;
            const num = newValue.replace(/\D/g, '');
            const len = num.length;
            const lenBefore = newValue.slice(0, pos).replace(/\D/g, '').length;
            const diff = pos - lenBefore;

            // 输入手机号格式为xxx xxxx xxxx
            if (len <= 3) {
                this.formattedPhoneNumber = num.slice(0);
            } else if (len > 3 && len <= 7) {
                this.formattedPhoneNumber = `${num.slice(0, 3)} ${num.slice(3)}`;
            } else if (len > 7) {
                this.formattedPhoneNumber = `${num.slice(0, 3)} ${num.slice(3, 7)} ${num.slice(7)}`;
            }

            // 设置光标位置
            if (lenBefore === 3 && diff !== 0) {
                pos = lenBefore;
            } else if (lenBefore > 3 && lenBefore <= 7 && diff !== 1) {
                pos = lenBefore + 1;
            } else if (lenBefore > 7 && diff !== 2) {
                pos = lenBefore + 2;
            }

            if (this.$refs.input.setSelectionRange) {
                this.$refs.input.setSelectionRange(pos, pos);
                this.$refs.input.focus();
            }
        },
    },
    methods: {
        focusInput() {
            this.$refs.input.focus();
        },
        phoneNumberInputHandler(e) {
            this.phoneNumber = e.target.value.replace(/\D/g, '');
            this.$emit('input', this.phoneNumber);
        },
        clearHandler() {
            this.phoneNumber = '';
            this.$emit('input', '');
        },
    },
};
</script>

