<template>
    <div class="withdraw__base">
        <div class="balance">
            <div class="balance__info">
                <div class="balance__label">我的现金：</div>
                <div class="balance__coin">
                    <span class="balance__coin-num">{{ withdrawInfo.cash | formatCash }}</span>
                    <span class="balance__triangle" />
                    <span class="balance__cash">等于<span class="mitype2018">{{ withdrawInfo.coin | formatNumber }}</span>金币</span>
                </div>
            </div>
            <span
                class="withdraw__progress"
                @click="$emit('goProgress')">提现进度</span>
        </div>
        <div
            class="withdraw-tip">
            <i class="mark-icon"/>
            若连续30天未登录，未提现的收益将过期清空
        </div>
        <div
            v-if="withdrawInfo.withdrawOptions && withdrawInfo.withdrawOptions.length !== 0"
            class="withdraw-options">
            <div
                class="withdraw__label"
                v-if="wechatOptions.length !== 0">
                <img
                    class="withdraw__label__icon"
                    src="./image/wechat-icon.png">
                <div class="withdraw__label_txt">微信提现</div>
            </div>
            <withdraw-option
                v-for="option in wechatOptions"
                :key="`${option.type}-${option.cash}`"
                :value="value"
                :new-imei='newImei'
                :cash="withdrawInfo.cash"
                :option="option"
                @click.native="clickHandler(option)" />
            <div
                v-if="!isnewuser"
                class="withdraw-options__option withdraw-coupon"
                @click="clickCoupon"
                />
            <div
                class="withdraw__label"
                v-if="phoneOptions.length !== 0">
                <img
                    class="withdraw__label__icon"
                    src="./image/phone-icon.png">
                <div class="withdraw__label_txt">话费充值</div>
            </div>
            <withdraw-option
                v-for="option in phoneOptions"
                :key="`${option.type}-${option.cash}`"
                :value="value"
                :new-imei='newImei'
                :cash="withdrawInfo.cash"
                :option="option"
                @click.native="clickHandler(option)" />
        </div>
        <div
            v-else
            :class="{ 'withdraw__empty-newuser' :isnewuser}"
            class="withdraw__empty">
            <div class="withdraw__empty-icon" />
            <p class="withdraw__empty-text">
                {{ isnewuser ? '阅读领红包，账户暂无现金可提现快去阅读吧': '微信提现和话费充值正在升级中，请耐心等待~' }}
            </p>
        </div>
        <div class="withdraw__hint">
            <p>注意事项:</p>
            <template v-if="!$route.query.channel">
                <p><span class="text-space">1</span>.每天定时进行额度发放，先到先得</p>
                <!-- eslint-disable-next-line max-len -->
                <p><span class="text-space">2</span>.连续签到<span v-if="limitSignDays!==0">{{ limitSignDays }}天</span>即可提现，两次提现间隔需大于3天</p>
                <p><span class="text-space">3</span>.如兑换遇到问题，请在「想看资讯」公众号里反馈</p>
                <p><span class="text-space">4</span>.最终解释权归想看App所有</p>
            </template>
            <template v-else>
                <p><span class="text-space">1</span>.提现最低0.3元，最低提现单位为0.1元，不足0.1元可明日提现。</p>
                <!-- eslint-disable-next-line max-len -->
                <p><span class="text-space">2</span>.挑战提现申请将在10分钟内审批到账，如遇节假日时间顺延</p>
                <p><span class="text-space">3</span>.因微信提现和话费供应商每日有额度上限，若遇到提示“今日额度已发完”，请明日再来兑换</p>
                <p><span class="text-space">4</span>.如兑换遇到问题，请在「我的」页面选择“帮助反馈”</p>
                <p><span class="text-space">5</span>.最终解释权归想看App所有</p>
            </template>
        </div>
        <div class="withdraw__btn">
            <Button
                v-if="withdrawInfo.withdrawOptions && withdrawInfo.withdrawOptions.length"
                :loading="loading"
                :disabled="!value.coin || withdrawInfo.coin < 3000"
                :class="{'animation-breathing': newImei}"
                class="bg-green"
                @click="readOrDraw">
                <i
                    class="withdraw-icon"
                    v-if="(value.type === 'wechat' && !needRead) || value.buttonMsg" />
                <span>{{ buttonText }}</span>
            </Button>
            <Button
                v-else
                class="bg-green"
                @click="$emit('goTask')">去赚金币</Button>
        </div>
    </div>
</template>
<script>
import Button from '@/components/Button';
import { goIndexTab } from '@/utils/jsApiV2/deeplink';
import { openCommonWebViewByPath } from '@/utils/jsApiV2/client';
import { formatNumber, formatCash } from '@/filters';
import WithdrawOption from './WithdrawOption';

export default {
    name: 'WithdrawBase',
    data() {
        return {
            isnewuser: this.$route.query.channel,
        };
    },
    components: {
        Button,
        WithdrawOption,
    },
    props: {
        withdrawInfo: {
            type: Object,
            default: () => ({}),
        },
        value: {
            type: Object,
            default: null,
        },
        withdrawHandler: {
            type: Function,
            default: () => {},
        },
        loading: {
            type: Boolean,
            default: false,
        },
        readTime: {
            type: Number,
            default: 0,
        },
        newImei: {
            type: Boolean,
            default: false,
        },
    },
    filters: {
        formatNumber, formatCash,
    },
    computed: {
        wechatOptions() {
            return this.withdrawInfo.withdrawOptions.filter(value => value.type === 'wechat');
        },
        phoneOptions() {
            return this.withdrawInfo.withdrawOptions.filter(value => value.type === 'phonenumber');
        },
        limitSignDays() {
            return (this.value && this.value.limitSignDays) || 0;
        },
        needRead() {
            if (this.value.readLimitTime > 0) {
                if (this.readTime < this.value.readLimitTime) {
                    return true;
                }
            }
            return false;
        },
        buttonText() {
            if (this.value.buttonMsg) {
                return this.value.buttonMsg;
            } else if (this.needRead) {
                return `去阅读${this.value.readLimitTime - this.readTime}分钟,再提现`;
            } else if (this.value.type === 'wechat') {
                return '提现到微信';
            }
            return '立即兑换';
        },
    },
    methods: {
        readOrDraw() {
            if (this.needRead && !this.value.buttonMsg) {
                this.$o2o({
                    e_c: '提现',
                    e_a: '点击',
                    e_n: '立即提现',
                    e_x: JSON.stringify({
                        string_type: `微信${this.value.cash / 100}元`,
                        status: '去阅读',
                    }),
                });
                goIndexTab('newsTab');
            } else {
                this.$emit('withdraw');
            }
        },
        clickHandler(cash) {
            if (cash.canClick) {
                this.$emit('input', cash);
            }
        },
        clickCoupon() {
            this.$o2o({
                e_c: '提现兑换入口',
                e_a: '点击',
                e_n: '金币兑换现金券',
            });
            openCommonWebViewByPath('/mobile-v2/act/pddExchange');
        },
    },
    created() {
        // 新用户7天不曝光金币兑换现金券
        if (!this.isnewuser) {
            this.$o2o({
                e_c: '提现兑换入口',
                e_a: '曝光',
                e_n: '金币兑换现金券',
            });
        }
    },
};
</script>
