<template>
    <div class="withdraw__wechat">
        <withdraw-base
            :withdraw-info="withdrawInfo"
            :read-time="readTime"
            :new-imei='newImei'
            v-model="selectedCash"
            :loading="loading"
            @goTask="goTask"
            @goProgress="goProgress"
            @withdraw="withdrawHandler(handleWithdrawSuccess, handleWithdrawError)" />
        <withdraw-dialog
            title="话费充值"
            :outside-close="false"
            :show.sync="showPhoneDialog"
            @close="initPhonenumber">
            <withdraw-phone-input v-model="phonenumber" />
            <Button
                :customStyle="{
                    width: '100%',
                    height: '0.43rem',
                    fontSize: '0.16rem',
                    background: '#0EC217'
                }"
                @click="handlePhoneWithdraw"
                :disabled="!phonenumber">确认充值</Button>
        </withdraw-dialog>
        <withdraw-dialog
            :msgs="reasons"
            :btn-text="reasonBtnText"
            :click-handler="reasonHandler"
            :show.sync="showReasonDialog" />
        <withdraw-dialog
            title="登录提示"
            :show.sync="showLoginDialog">
            <div class="login-tip">「{{ wechatNickname }}」已经是想看用户了<br>是否直接登录？</div>
            <div class="login-btns">
                <Button
                    :customStyle="{
                        height: '0.47rem',
                        fontSize: '0.15rem',
                        color: '#f64d51',
                        backgroundColor: '#fff',
                        border: '0.01rem solid #f64d51'
                    }"
                    @click="closeLoginDialog">取消</Button>
                <Button
                    :customStyle="{
                        height: '0.47rem',
                        fontSize: '0.15rem',
                        color: '#fff',
                        backgroundColor: '#f64d51',
                        border: '0.01rem solid #f64d51',
                        marginLeft: '0.13rem'
                    }"
                    @click="handleLogin">继续登录</Button>
            </div>
        </withdraw-dialog>
        <WithdrawApplyEntrance
            v-if="showWithdrawApplyDialog"
            :is-applying="withdrawInfo.appealStatus === '1'"
            @close="closeWithdrawApplyDialog"
        />
    </div>
</template>

<script>
import {
    toast,
    openCommonWebViewByPath,
    hasCalendarEventExist,
} from '@/utils/jsApiV2/client';
import { goTab } from '@/utils/jsApiV2/deeplink';
import { get, set, remove } from '@/utils/cache';
import config from '@/config';
import { addCustomEventListener, removeCustomEventListener } from '@/utils/event';
import WithdrawDialog from './WithdrawDialog';
import WithdrawApplyEntrance from './WithdrawApplyEntrance';
import WithdrawPhoneInput from './WithdrawPhoneInput';
import WithdrawBase from './WithdrawBase';
import withdrawMixin from './withdrawMixin';
import withdrawVisitorMixin from './withdrawVisitorMixin';
import { cacheSelectedCashKey } from './config';

export default {
    name: 'WithdrawWechat',
    mixins: [withdrawMixin, withdrawVisitorMixin],
    data() {
        return {
            showReasonDialog: false,
            showPhoneDialog: false,
            reasons: [],
            reasonBtnText: '',
            reasonHandler: this.closeReasonDialog,
            defaultReasons: ['网络不太好，请稍后重试'],
            defaultReasonBtnText: '我知道了',
            showWithdrawApplyDialog: false,
        };
    },
    components: {
        WithdrawBase,
        WithdrawDialog,
        WithdrawPhoneInput,
        WithdrawApplyEntrance,
    },
    props: {
        newImei: {
            type: Boolean,
            default: false,
        },
    },
    watch: {
        isAuth(newVal) {
            if (newVal) {
                this.checkPhoneWithdraw();
            }
        },
        // 进入提现兑换页面，如果金币资产充足，则选中满足条件的第一个金额档位
        withdrawInfo(newVal) {
            if (!this.selectedCash) {
                this.selectedCash = this.getMinCashOption(newVal.withdrawOptions);
            }
        },
    },
    methods: {
        closeWithdrawApplyDialog(isComfirm) {
            this.showWithdrawApplyDialog = false;
            if (isComfirm) {
                addCustomEventListener('focus', this.focusHandler);
            }
        },
        focusHandler() {
            removeCustomEventListener('focus', this.focusHandler);
            window.location.reload();
        },
        goTask() {
            if (config.versionCode > 40830) {
                // 40840新加的tab
                goTab('chatTab');
            } else if (config.versionCode >= 40600) {
                goTab('mineTab');
            } else {
                goTab('taskTab');
            }
        },
        /*
            * withdraw提现接口目前有两种用法：
            * 当type为phonenumber且phonenumber为空时，用于校验用户的提现资格（当用户具有资格时，弹手机号输入框）
            * 当type为phonenumber且phonenumber不为空，当type为wechat时，用于发起提现
            *
            * handleWithdrawSuccess方法用于处理这两种用法的成功响应情况
        */
        handleWithdrawSuccess(res) {
            // 账号异常
            if (res.status === 2000) {
                if (config.versionCode < 40850) {
                    toast('账号异常、升级新版APP可申诉');
                } else {
                    this.showWithdrawApplyDialog = true;
                }
                return;
            }
            if (this.newImei) {
                this.$emit('clickGuideHand');
            }
            if (this.selectedCash.type === 'phonenumber'
                && !this.phonenumber) {
                this.openPhoneDialog();
            } else if ((this.selectedCash.type === 'phonenumber' && this.phonenumber)
            || this.selectedCash.type === 'wechat') {
                if (this.showPhoneDialog) {
                    this.closePhoneDialog();
                }

                this.$o2o({
                    e_c: '提现',
                    e_a: '成功',
                    e_n: '提现成功',
                    e_x: {
                        string_type: `${this.typeMap[this.selectedCash.type]}${this.selectedCash.cash / 100}元`,
                    },
                }, () => {
                    this.commitWithdraw(res);
                });
            }
        },
        handleWithdrawError(res) {
            if (this.newImei) {
                this.$emit('clickGuideHand');
            }
            if (res.data) {
                this.checkCode(res.data);
            } else if (res.msg) {
                toast(res.msg);
            } else {
                toast('网络异常，请稍后重试');
            }
        },
        checkCode(res) {
            set(cacheSelectedCashKey, JSON.stringify(this.selectedCash));
            switch (res.code) {
            case 14:
                this.configReasonDialog({
                    msg: res.msg,
                    btnText: '去认证',
                    handler: this.goAuth,
                });
                break;
            case 18:
                if (res.expMonStatus) {
                    this.configReasonDialog({
                        msg: `${res.msg}|可尝试兑换0.3元，秒到账！`,
                        btnText: this.defaultReasonBtnText,
                        handler: this.closeReasonDialog,
                    });
                } else {
                    this.configReasonDialog({
                        msg: res.msg,
                        btnText: '做任务赚金币',
                        handler: this.goTask,
                    });
                }
                break;
            case 20:
                this.configReasonDialog({
                    msg: res.msg,
                    btnText: '去做任务',
                    handler: this.goTask,
                });
                break;
            case 23:
                if (!res.expMonStatus && config.versionCode >= 40770) {
                    const title = '【想看】今天可提现1元零钱了，点击提现>>';
                    const description = '想看1元提现';

                    const hasCalendar = !!hasCalendarEventExist(title, description);

                    if (hasCalendar) {
                        this.configReasonDialog({
                            msg: res.msg,
                            btnText: this.defaultReasonBtnText,
                            handler: this.closeReasonDialog,
                        });
                    } else {
                        this.configReasonDialog({
                            msg: res.msg,
                            btnText: '加入日历提醒，不错过提现',
                            handler: () => {
                                this.handleAddCalendar(title, description);
                                this.closeReasonDialog();
                            },
                        });
                    }
                } else {
                    this.configReasonDialog({
                        msg: res.msg,
                        btnText: this.defaultReasonBtnText,
                        handler: this.closeReasonDialog,
                    });
                }

                break;
            case 24:
                if (this.selectedCash.cash === 30) {
                    this.handleBindWechat(() => {
                        this.withdrawHandler(this.handleWithdrawSuccess,
                            this.handleWithdrawError);
                    });
                } else {
                    this.configReasonDialog({
                        msg: res.msg,
                        btnText: '绑定并兑换',
                        handler: () => {
                            this.closeReasonDialog();
                            this.handleBindWechat(() => {
                                this.withdrawHandler(this.handleWithdrawSuccess,
                                    this.handleWithdrawError);
                            });
                        },
                    });
                }

                break;
            case 25:
                this.configReasonDialog({
                    msg: res.msg,
                    btnText: '绑定并兑换',
                    handler: () => {
                        this.closeReasonDialog();
                        this.handleBindPhone();
                    },
                });
                break;
            case 29:
                toast(res.msg || '提现失败');
                // 表示成功提交了订单，但是最后失败了
                openCommonWebViewByPath('/mobile-v2/withdraw/progress');
                break;
            default:
                this.configReasonDialog({
                    msg: res.msg,
                    btnText: this.defaultReasonBtnText,
                    handler: this.closeReasonDialog,
                });
                break;
            }
        },
        handlePhoneWithdraw() {
            const regexp = /^1[0-9]{10}$/;

            if (!regexp.test(this.phonenumber)) {
                toast('请输入正确的手机号码！');
            } else {
                this.withdrawHandler(this.handleWithdrawSuccess, (res) => {
                    this.closePhoneDialog();
                    this.handleWithdrawError(res);
                });
            }
        },
        getMinCashOption(options) {
            let option;

            for (let i = 0; i < options.length; i += 1) {
                option = options[i];
                if (option.canClick) {
                    return option;
                }
            }
            return {};
        },
        /*
            * 产品要求如果用户一开始选择话费后去认证页，返回提现页面后要弹出手机号输入框
            * 返回提现页面后弹出手机号输入框的判断条件：
            * 1. isAuth由false变为true
            * 2. 用户选择了话费
            * 3. 缓存中的类型为话费
        */
        checkPhoneWithdraw() {
            const cachedCash = JSON.parse(get(cacheSelectedCashKey));

            if (this.selectedCash
            && cachedCash
            && this.selectedCash.type === 'phonenumber'
            && cachedCash.type === 'phonenumber'
            && this.isAuth) {
                this.openPhoneDialog();
            }

            remove(cacheSelectedCashKey);
        },
        configReasonDialog({ msg, btnText, handler }) {
            this.reasons = msg ? msg.split('|') : this.defaultReasons;
            this.reasonBtnText = btnText;
            this.reasonHandler = handler;

            this.openReasonDialog();
        },
        openReasonDialog() {
            this.showReasonDialog = true;
        },
        closeReasonDialog() {
            this.showReasonDialog = false;
        },
        openPhoneDialog() {
            this.phonenumber = this.authPhonenumber;
            this.showPhoneDialog = true;
        },
        closePhoneDialog() {
            this.initPhonenumber();
            this.showPhoneDialog = false;
        },
        initPhonenumber() {
            this.phonenumber = '';
        },
        /*
            * 去认证前缓存下用户当前选择的提现面值
            * 因为认证成功后，如果是微信提现则直接发提现请求
            * 如果话费提现也需要根据用户当前选择的提现面值进行其他判断（见checkPhoneWithdraw）
        */
        goAuth() {
            this.closeReasonDialog();
            set(cacheSelectedCashKey, JSON.stringify(this.selectedCash));
            let url = '/mobile-v2/withdraw/auth';
            // 大额提现（10元以上）需要强制校验身份证姓名信息
            if (this.selectedCash.cash >= 1000 && this.pageType !== 'CHANNEL_NEW_USER') {
                url += '?forceAuth=1';
            }
            openCommonWebViewByPath(url);

            this.$o2o({
                e_c: '提现',
                e_a: '点击',
                e_n: '去认证',
            });
        },
    },
};
</script>
<style scoped>
.withdraw__wechat {
    overflow: auto;
    margin-bottom: 80px;
}

.withdraw__wechat >>> .gray-mask {
    bottom: 73px !important;
    top: -73px !important;
}

.pluse {
    position: absolute;
    width: 50px;
    height: 50px;
    right: 47px;
    bottom: -60px;
    border-radius: 50%;
    border: 5px solid rgba(255, 255, 255, 0.8);
    opacity: 1;
    animation: warn 1.5s infinite ease-in;
}

@keyframes warn {
    0% {
        transform: scale(0);
        opacity: 0;
    }

    25% {
        transform: scale(0);
        opacity: 0.1;
    }

    50% {
        transform: scale(0.1);
        opacity: 0.3;
    }

    75% {
        transform: scale(0.5);
        opacity: 0.5;
    }

    100% {
        transform: scale(1);
        opacity: 0;
    }
}

.guide-hand {
    width: 100px;
    height: 72px;
    background: url(./image/guide-hand.gif);
    background-size: 100px 72px;
    position: absolute;
    bottom: -80px;
    right: 15px;
    z-index: 2;
}

.reason {
    font-size: 17px;
    line-height: 22px;
    text-align: center;
    margin-bottom: 35px;
}

.login-tip {
    font-size: 15px;
    line-height: 21px;
    opacity: 0.7;
    text-align: center;
    margin-bottom: 22px;
}

.login-btns {
    display: flex;
    justify-content: space-between;
}

.icon {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 25px;
    font-size: 15px;
    color: rgba(0, 0, 0, 0.7);
}

.icon::before {
    content: "";
    width: 151px;
    height: 117px;
    background: url(./image/calendar.webp) no-repeat;
    background-size: 100% 100%;
}
</style>
